img {
  width: 100%;
}
.wrap {
  width: 928px;
  margin: 0 auto;
}
/*thImg*/
.thImg {
  width: 880px;
  margin: 0 auto;
  position: relative;
  height: 310px;
}
.thImg div {
  position: absolute;
  padding: 15px;
  top: -50px;
  left: 0;
  background: url(../img/bg.jpg) #fff repeat-x bottom;
}
.thImg a {
  float: left;
  width: 260px;
  height: 350px;
  margin-right: 15px;
  overflow: hidden;
}
.thImg a:last-child {
  margin-right: 0;
}
.thImg a img {
  display: block;
  transition: all 1s ease;
  -webkit-transition: all 1s ease;
}
.thImg a:hover img {
  transform: scale(1.1, 1.1);
  -webkit-transform: scale(1.1, 1.1);
}

/*news*/
.news,
.people {
  margin-top: 50px;
}
.news h2 img,
.people h2 img {
  width: 70%;
  display: block;
  margin: 0 auto 30px;
}
.news div.top a,
.news div.bott a {
  display: block;
  position: relative;
}
.news div.top a p,
.news div.bott a p {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  opacity: 0;
  transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
}
.news div.top a:hover p,
.news div.bott a:hover p {
  opacity: 0.3;
}

.news div.top a,
.news div.bott a {
  float: left;
}
.news div.top,
.news div.bott {
  margin-bottom: 35px;
}
.news div.top a:nth-child(2),
.news div.bott a:nth-child(2) {
  margin: 0 35px;
}
.news .top img,
.news .bott img {
  display: block;
}
.news .flower {
  display: flex;
  justify-content: space-around;
  text-align: center;
  color: #777;
}
.news .flower a {
  display: block;
}
.news .flower dl dd span {
  display: inline-block;
  padding: 10px;
  color: #a10000;
  border: 1px solid #bababa;
  font-size: 16px;
  font-weight: bold;
  background: #fff;
  transition: all 0.8s ease;
  -webkit-transition: all 0.8s ease;
}
.news .flower dl dd span:hover {
  background: #a10000;
  color: #fff;
  transform: translate();
  border: 1px solid #a10000;
}
.news .flower dl dd:nth-child(2) {
  margin: 22px 0;
}

.news .flower dl dd:nth-child(3) {
  margin-bottom: 50px;
}

/**********************own**/
.tran dl dt img {
  display: block;
}
.tran dl dt {
  position: relative;
  overflow: hidden;
}
.tran dl dt span {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #90d7ec;
  opacity: 0.2;
}
.tran dl dt span.abl {
  left: -100%;
  top: -100%;
}
.tran dl dt span.abr {
  left: 100%;
  top: 100%;
}
.tran dl dt:hover span {
  left: 0;
  top: 0;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
}

/*ad*/
a.ad {
  display: block;
}

/*people*/
.people .pList {
  display: flex;
  justify-content: space-around;
}
.people .pList a {
  display: block;
  margin-bottom: 20px;
  text-align: center;
}
.people .pList a:hover {
  box-shadow: 0 0 10px #757575;
}

.people .pList dl dd:nth-child(2) {
  line-height: 25px;
}
.people .pList dl dd span {
  display: inline-block;
  color: #a10000;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 10px;
}

